.wraper {
   display: flex;
   flex-direction: column;
   align-items: center;
   width: 600px;
   height: 100%;
   background-color: rgb(87, 84, 84);
   word-break: break-all;
   border-radius: 20px;
}

.wraperContainer {
   display: flex;
   flex-direction: column;
   justify-content: space-around;
   align-items: center;
   padding-bottom: 10px;
}

.aboutHeader {
   display: flex;
   justify-content: space-around;
   width: 100%;
   height: 45px;
   padding-top: 15px;

}

.aboutHeader div {
   display: flex;
   justify-content: center;
   align-items: center;
   border: none;
   border-radius: 10px 10px 0 0;
   cursor: pointer;
   color: rgb(22, 191, 233);
   padding-top: 0;
   width: 80px;
   
}

.aboutContent {
   display: flex;
   justify-content: center;
   width: 100%;
   height: 90%;
   
}